import { Component, h, State, Element, Event, EventEmitter, Listen } from '@stencil/core';

@Component({
  tag: 'rf-first',
  styleUrl: './first.css',
  shadow: true,
})
export class First {
  @Element() element: HTMLElement;
  divDates: HTMLDivElement;
  nextBtn: HTMLButtonElement;
  prevBtn: HTMLButtonElement;
  monthInput: HTMLSelectElement;
  yearInput: HTMLInputElement;
  dateInput: HTMLInputElement;

  @State() isOpen = false;
  open() {
    this.isOpen = !this.isOpen;
  }

  // handle apply button click
  handleApplyBtn() {
    this.dateInput.value = this.selectedDate.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' });
  }

  // handle prev month nav
  handlePrevBtn() {
    this.sub_month.emit({ month: this.month, year: this.year });
    if (this.month === 0) {
      this.year--;
    }
    this.month = (this.month - 1 + 12) % 12;
    this.displayDates();
  }

  //  handle month input change
  changeMonth() {
    this.month = this.monthInput.selectedIndex;
    this.displayDates();
    this.upd_month.emit({ month: this.month, year: this.year });
  }

  // handle year input change event
  changeYear() {
    this.year = Number(this.yearInput.value);
    this.displayDates();
    this.upd_year.emit({ month: this.month, year: this.year });
  }

  updateYearMonth() {
    this.monthInput.selectedIndex = this.month;
    this.yearInput.value = this.year.toString();
  }

  handleClick(dates: HTMLDivElement, e: Event) {
    const button = e.target as HTMLButtonElement;
    //remove the 'selected' class from other buttons
    const selected = dates.querySelector('.selected');

    // selected && selected.classList.remove('selected');

    // add the 'selected' class to current button
    button.classList.add('selected');

    // set the selected date
    this.selectedDate = new Date(this.year, this.month, parseInt(button.textContent));
    if (this.is_second_selected) {
      this.close_cal.emit(this.selectedDate);
    } else if (selected) {
      this.send_second_date.emit(this.selectedDate);
      this.hide_cal.emit(true);
    } else {
      this.send_first_date.emit(this.selectedDate);
    }
  }

  selectedDate = new Date();
  @State() year = this.selectedDate.getFullYear();
  @State() month = this.selectedDate.getMonth();

  @Event({ bubbles: true, composed: true }) hide_cal: EventEmitter<boolean>;
  @Event({ bubbles: true, composed: true }) send_first_date: EventEmitter<Date>;
  @Event({ bubbles: true, composed: true }) send_second_date: EventEmitter<Date>;

  @Event({ bubbles: true, composed: true }) sub_month: EventEmitter<{ month: number; year: number }>;
  @Event({ bubbles: true, composed: true }) upd_month: EventEmitter<{ month: number; year: number }>;
  @Event({ bubbles: true, composed: true }) upd_year: EventEmitter<{ month: number; year: number }>;

  @Listen('add_month', { target: 'body' })
  listenMonth(event: CustomEvent) {
    this.month = event.detail.month;
    this.year = event.detail.year;
    this.displayDates();
  }

  is_second_selected = false;
  @Listen('send_first_date', { target: 'body' })
  getFirstDate() {
    this.is_second_selected = true;
  }

  @Listen('updt_month', { target: 'body' })
  updM(event: CustomEvent) {
    this.month = event.detail.month;
    this.year = event.detail.year;
    if (this.month === 0) {
      this.year--;
    }
    this.month = (this.month - 1 + 12) % 12;
    this.displayDates();
  }

  @Event({ bubbles: true, composed: true }) close_cal: EventEmitter<Date>;

  @Listen('updt_year', { target: 'body' })
  updY(event: CustomEvent){
    this.year = event.detail.year
    if (this.month === 11) {
      this.year--;
    }
    this.displayDates()
  }

  //render the dates in the calendar interface
  displayDates = () => {
    const dates = this.divDates;
    //update year & month whenever the dates are updated
    this.updateYearMonth();

    // clear the dates
    dates.innerHTML = '';
    //* display the last week of previous month

    //get the last date of previous month
    const lastOfPrevMonth = new Date(this.year, this.month, 0);
    for (let i = 0; i <= lastOfPrevMonth.getDay(); i++) {
      const text = lastOfPrevMonth.getDate() - lastOfPrevMonth.getDay() + i;
      const button = this.createButton(text.toString(), true, -1);
      dates.appendChild(button);
    }
    //* display the current month

    //get the last date of the month
    const lastOfMonth = new Date(this.year, this.month + 1, 0);
    for (let i = 1; i <= lastOfMonth.getDate(); i++) {
      const button = this.createButton(i.toString(), false);
      const currentDate = new Date()
      if(this.month < currentDate.getMonth()){
        button.disabled = true
      }
      if( i < currentDate.getDay()+ 1){
        button.disabled = true
      }
      button.addEventListener('click', event => this.handleClick(dates, event));
      dates.appendChild(button);
    }
    //* display the first week of next month
    const firstOfNextMonth = new Date(this.year, this.month + 1, 1);
    for (let i = firstOfNextMonth.getDay(); i < 7; i++) {
      const text = firstOfNextMonth.getDate() - firstOfNextMonth.getDay() + i;
      const button = this.createButton(text.toString(), true, 1);
      dates.appendChild(button);
    }
  };

  createButton = (text: string, isDisabled = false, type = 0) => {
    const currentDate = new Date();

    // determine the date to compare based on the button type
    let comparisonDate = new Date(this.year, this.month + type, Number(text));

    // check if the current button is the date today
    const isToday = currentDate.getDate().toString() === text && currentDate.getFullYear() === this.year && currentDate.getMonth() === this.month + type;

    // check if the current button is selected
    const selected = this.selectedDate.getTime() === comparisonDate.getTime();

    const button = document.createElement('button');
    button.textContent = text;
    button.disabled = isDisabled;
    button.classList.toggle('today', isToday); //if is today true,add class 'today'
    button.classList.toggle('selected', selected);
    return button;
  };

  componentDidLoad() {
    this.displayDates();
  }
  render() {
    return (
      <div class="datepicker-container">
        <div class="datepicker">
          <div class="datepicker-header">
            <button class="prev" ref={el => (this.prevBtn = el)} onClick={this.handlePrevBtn.bind(this)}>
              Prev
            </button>

            <div>
              <select class="month-input" ref={el => (this.monthInput = el)} onChange={this.changeMonth.bind(this)}>
                <option>January</option>
                <option>February</option>
                <option>March</option>
                <option>April</option>
                <option>May</option>
                <option>June</option>
                <option>July</option>
                <option>August</option>
                <option>September</option>
                <option>October</option>
                <option>November</option>
                <option>December</option>
              </select>
              <input type="number" class="year-input" ref={el => (this.yearInput = el)} onInput={this.changeYear.bind(this)} />
            </div>
          </div>

          <div class="days">
            <span>Sun</span>
            <span>Mon</span>
            <span>Tue</span>
            <span>Wed</span>
            <span>Thu</span>
            <span>Fri</span>
            <span>Sat</span>
          </div>

          <div class="dates" ref={el => (this.divDates = el)}></div>
        </div>
      </div>
    );
  }
}
